.question-answer {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	.content {
		display: flex;
		flex-direction: row;
		height: 100%;
		.left-content {
			flex-grow: 1;
			display: flex;
			flex-direction: column;
			width: fit-content;
			.sub-header {
				display: flex;
				justify-content: space-between;
				flex-direction: row;
				padding-right: 1rem;
				height: 34px;
				background: #ffffff 0% 0% no-repeat padding-box;
				box-shadow: 0px 3px 6px #0000000d;
				color: #3e3e3e;
				.marks {
					font-size: 14px;
					line-height: 34px;
				}
			}
			.question-wrapper {
				flex: 1;
				height: 100%;
				margin: 1rem 1rem 0 1rem;
				border-radius: 6px;
				.question-content {
					display: flex;
					flex-direction: column;
					height: calc(100% - 45px);
					background: #ffffff 0% 0% no-repeat padding-box;
					box-shadow: 0px 3px 16px #0000001a;
					.question-description {
						display: flex;
						justify-content: flex-start;
					}
					.divider {
						display: flex;
						flex-direction: row;
						border-bottom: 1px solid #0000001a;
					}
				}
				.footer {
					margin-top: 10px;
				}
			}
		}
		.right-content {
			min-width: 240px;
			max-width: 240px;
			background-color: #ffffff;
			.header {
				height: 34px;
				font-size: 14px;
				line-height: 34px;
				letter-spacing: unset;
				padding: 0px 0.5rem;
				color: #ffffff;
				background: #617b8c 0% 0% no-repeat padding-box;
				box-shadow: 0px 3px 6px #0000000d;
			}
			p {
				font-size: 14px;
				color: #617b8c;
				padding: 0.5rem;
			}
			.question-list {
				padding: 0.5rem;
				ul {
					display: flex;
					flex-wrap: wrap;
					li {
						margin: 0px 5px 5px 0px;
						// .question-index.active {
						//   background: #cd3805;
						//   color: #ffffff;
						// }
						.question-index:hover {
							background: #1e91ff;
							color: #ffffff;
						}
						button {
							width: 30px;
							height: 30px;
							padding: 0px;
							border-radius: 3px;
							color: #ffffff;
							border-width: 0px;
							background: transparent
								linear-gradient(180deg, #eeeeee 0%, #cccccc 100%) 0% 0%
								no-repeat padding-box;
						}
					}
				}
			}
			.question-type {
				padding: 0.5rem;
				ul {
					display: flex;
					flex-wrap: wrap;
					font-size: 13px;
					li {
						display: flex;
						width: 112px;
						.count {
							min-width: 25px;
							text-align: center;
							line-height: 25px;
							height: 25px;
							border-radius: 13px;
							color: #ffffff;
						}
						.label {
							color: #3e3e3e;
							margin-left: 0.5rem;
						}
					}
					//   li {
					//     width: 100%;
					//     margin: 0 0 15px 0;
					//   }
					//   li a {
					//     float: left;
					//     width: 100%;
					//     font-size: 13px;
					//   }
					//   li a span:last-child {
					//     width: 84%;
					//     padding-top: 3px;
					//     float: left;
					//   }
					//   li:last-child a span:first-child {
					//     margin-top: 6px;
					//   }
					//   li a span:first-child {
					//     width: 25px;
					//     margin-right: 8px;
					//     float: left;
					//     height: 25px;
					//     font-size: 11px;
					//     border-radius: 100%;
					//     text-align: center;
					//     color: #fff;
					//     line-height: 25px;
					//   }
				}
			}
		}
	}
}
