.test-response {
	display: flex;
	flex-direction: column;
	height: 100%;
	font-size: 1rem;
	.response-header {
		display: flex;
		justify-content: space-around;
		margin: 1rem;
		.answer-details {
			background: #ffffff 0% 0% no-repeat padding-box;
			box-shadow: 0px 3px 1rem #0000001a;
			padding: 1rem;
			width: 30%;
			border-radius: 8px;
		}
		.user-info {
			background: #ffffff 0% 0% no-repeat padding-box;
			box-shadow: 0px 3px 1rem #0000001a;
			padding: 1rem;
			width: 30%;
			border-radius: 8px;
		}
	}
	.question-wrapper {
		display: flex;
		flex-direction: column;
		flex: 1;
		height: 100%;
		margin: 1rem 1rem 0 1rem;
		border-radius: 6px;
		.question-content {
			display: flex;
			flex-direction: column;
			flex: 1 auto;
			height: calc(100% - 45px);
			background: #ffffff 0% 0% no-repeat padding-box;
			box-shadow: 0px 3px 1rem #0000001a;
			.question-description {
				display: flex;
				justify-content: flex-start;
			}
			.answer-wrapper {
				font-size: 1rem;
			}
			.divider {
				display: flex;
				flex-direction: row;
				border-bottom: 1px solid #0000001a;
			}
		}
		.footer {
			margin-top: 10px;
		}
	}
	.correct-answer {
		position: relative;
		border: 2px solid green;
	}
	.not-correct-answer {
		position: relative;
		border: 2px solid red;
	}
	.correct-question-icon {
		position: absolute;
		right: 0;
		top: 0;
		padding: 0.5rem;
		.fa-check-circle {
			color: green;
		}
		.fa-times-circle {
			color: red;
		}
	}
}
