.copyrightBox .list-items li:hover:after{background-color:#51be78;}
.copyrightBox .copyrightBox-inner{display:-webkit-flex;display:-ms-flex;display:flex;-webkit-align-items:center;align-items:center;}
.copyrightBox .copyDesc{color:#fff; margin-bottom: 0px; }
.copyrightBox .copyDesc a{color:#1e90ff;transition:all 0.3s;}
.copyrightBox .copyDesc a:hover{color:#fff;}

.section-divider{display:inline-block;position:relative;height:5px;border-radius:30px;background-color:#51be78;width:90px;margin-top:25px;margin-bottom:25px;overflow:hidden;}
.section-divider:after{content:'';position:absolute;left:0;top:-1.1px;height:7px;width:8px;background-color:#fff;-webkit-animation-duration:3s;animation-duration:3s;-webkit-animation-timing-function:linear;animation-timing-function:linear;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-name:dot-move;animation-name:dot-move;}

.social-profile li{display:inline-block;}
.social-profile li a{color:#233d63;display:block;width:35px;height:35px;line-height:35px;text-align:center;border-radius:50%;background-color:rgba(35, 61, 99, 0.1);transition:all 0.3s;position:relative;z-index:1;}
.social-profile li a:hover{color:#fff;background-color:#51be78;}

.icon-element{color:#7E3CF9;font-size:50px;position:relative;width:80px;height:80px;line-height:85px;border-radius:50%;background-color:rgba(126, 60, 249, 0.1);text-align:center;transition:all 0.4s;display:inline-block;}
.icon--element{width:45px;height:45px;line-height:45px;font-size:24px;}
.theme-btn {font-size: 15px;text-transform: uppercase;background: #1e90ff;color: #fff;padding: 15px;line-height: 48px;border-radius: 5px;z-index: 1;display: inline-block;transition: all 0.3s;border: 1px solid #1e90ff;font-weight: 500; }

