.grid_30 {
	width: 30% !important;
	float: left;
}
.middleboxArea {
	width: 100%;
	float: left;
}
.grid_33 {
	width: 33%;
	float: left;
}
.grid_100 {
	width: 100%;
}
.errTag {
	width: 100%;
	padding: 12px;
	font-size: 15px;
}
.text-right {
	float: right;
}
.table {
	table-layout: fixed;
}
.table th,
.table td {
	word-break: break-all;
	white-space: normal !important;
	padding: 10px 5px !important;
}
.userviewdata {
	.user-img {
		margin: 1rem 0;
		.default-profile-image {
			font-size: 128px;
		}
		img {
			border: 1px solid #ddd;
			border-radius: 50%;
			height: 165px;
		}
	}
	ul {
		li {
			width: 50%;
			float: left;
			margin-bottom: 10px;
			b {
				width: 100%;
				float: left;
				font-size: 18px;
				font-weight: bold;
			}
			span {
				width: 100%;
				float: left;
			}
		}
	}
}
.registration {
	.dropdown .dropdown-menu {
		top: -10px !important;
		padding: 0px !important;
		inset: 1 !important;
	}
	.dropdown-item {
		padding: 0.5rem 0 0.5rem 0.5rem !important;
		margin: 0 !important;
		border-bottom: 1px solid #ebedf2;
		a {
			text-decoration: none;
			display: block;
		}
		a:hover {
		}
	}
}
